.menu-toggle {
//   position: absolute;
//   bottom: 400px;
  left: 52px;
  min-height: 40px;
  min-width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;

  margin: 0;
  padding: 0;
  color: $primary-accent-color;
  background-color: transparent;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  outline: none;
}

@media (min-width: 1200px) {
  .menu-toggle {
    display: none;
  }
}

